.title {
  font-weight: 600;
  font-size: 36px;
  line-height: 1.2;
  color: #0F2E74;
  margin-bottom: 50px;
}

.promo {
  &__inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__video-link {
    background-image: url('../../img/promo-poster.jpg');
    width: 995px;
    height: 496px;
    margin-bottom: 35px;
    display: flex;
    justify-content: center;
    align-items:center;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }

  &__link-more {
    border: 1px solid #0F2E74;
    border-radius: 15px;
    padding: 15px 70px;
    align-items:flex-start;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.2;
    color: #0F2E74;
  }
}
